<template>
  <div class="page-blog-edit">
      <div class="breadcrumb">
      <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{path:'/' }"></el-breadcrumb-item>
          <el-breadcrumb-item :to="{name:'blogList'}"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="form">
      <el-form>
        <el-form-item label="">
            <el-input v-model="blog.title" :maxlength="30"></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-input v-model="blog.author" :maxlength="30"></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-input v-model="blog.abstract" :maxlength="100"></el-input>
        </el-form-item>
        <el-form-item>
            <div ref="editor"></div>
        </el-form-item>
        <el-form-item>
          <el-button v-if="id" @click="saveBtnClick"></el-button>
          <el-button v-else @click="createBtnClick"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import E from 'wangeditor'
export default {
  data(){
    return {
       id:this.$route.query.id,
       editor:null,
       blog:{
         title:'',
         author:'',
         abstract:'',
         content:''
       }
    }
  },
  mounted(){
    this.initEditor();
  },
  methods:{
    //
    async getBlog(){
      let req = {id:this.id};
      let res = await this.$api.getBlogById(req);
      if(res.code == 200){
        this.blog = res.data||{};
        this.editor.txt.html(this.blog.content);//
      }else{
        this.$message.error("");
      }
    },
    //
    initEditor(){
      this.$nextTick(()=>{
        this.editor = new E(this.$refs.editor);
        this.editor.customConfig.menus = ['bold','italic','underline'];
        this.editor.customConfig.zIndex = 0;
        this.editor.create();
        //id
        if(this.id){
          this.getBlog();
        }
      })
    },
    //
    validateForm(){
      //
       if(this.blog.title==''){
         this.$message.warning('');
         return false;
       }
       if(this.blog.author==''){
         this.$message.warning('');
         return false;
       }
       if(this.blog.abstract==''){
         this.$message.warning('');
         return false;
       }
       return true;
    },
    //
    async saveBtnClick(){
       this.blog.content = this.editor.txt.html();
       if(!this.validateForm()){
          return false;
       }
       //
      let res = await this.$api.updateBlog(this.blog);
      if(res.code == 200){
        this.$message.success('');
        this.$router.back();
      }else{
        this.$message.error('');
      }
    },
    //
    async createBtnClick(){
        this.blog.content = this.editor.txt.html();
       if(!this.validateForm()){
          return false;
       }
       //
      let res = await this.$api.createBlog(this.blog);
      if(res.code == 200){
        this.$message.success('');
        this.$router.back();
      }else{
        this.$message.error('');
      }
    }
  }
}
</script>
<style lang="less" scoped>
.page-blog-edit{
   >.form{
     padding: 30px;
     width: 500px;
   }
}
</style>





